import { By, error } from 'selenium-webdriver';
import { TabBarMixedWebElementBase } from './TabBarMixedWebElementBase';

/**
 * The component WebElement for [oj-c-tab-bar-mixed](../../jsdocs/oj-c.TabBarMixed.html).
 * Do not instantiate this class directly, instead, use
 * [findTabBarMixed](../modules.html#findTabBarMixed).
 */
export class TabBarMixedWebElement extends TabBarMixedWebElementBase {
  // Put overrides here
  /**
   * Selects tab specified by key.
   * Triggers ojSelectionAction regardless if the key passed is same as the current selection value or not.
   * @param key key of the tab to be selected
   * @override
   * @typeparam K Type of keys
   */
  async doSelection<K>(key: K): Promise<void> {
    await this.whenReady();
    try {
      const tab = await this.findElement(By.css(`[data-oj-key="${key}"]`));
      const driver = this.getDriver();
      await driver.executeScript((el: Element) => el.scrollIntoView({ behavior: 'instant' }), tab);
      await tab?.click();
    } catch (e) {
      if (e instanceof error.NoSuchElementError) {
        console.error(`Tab with specified key "${key}" cannot be found due to error: ${e}`);
      }
      throw e;
    }
  }

  /**
   * Removes tab specified by key.
   * @param key key of the tab to be removed
   * @override
   * @typeparam K Type of keys
   */
  async doRemove<K>(key: K): Promise<void> {
    await this.whenReady();
    try {
      const tab = await this.findElement(By.css(`[data-oj-key="${key}"]`));
      const driver = this.getDriver();
      await driver.executeScript((el: Element) => el.scrollIntoView({ behavior: 'instant' }), tab);
      const button = await tab.findElement(By.css('[data-oj-tabbar-item-remove-icon="true"]'));
      await button?.click();
    } catch (e) {
      if (e instanceof error.NoSuchElementError) {
        console.error(`Tab with specified key "${key}" cannot be found due to error: ${e}`);
      }
      throw e;
    }
  }
}
